import { writable } from 'svelte/store';
export const taskListOptions = writable(readJsonFromStorage('options', {}));
taskListOptions.subscribe(val => writeJsonToStorage('options', val));
export const items = writable(readJsonFromStorage('items', []));
items.subscribe(val => writeJsonToStorage('items', val));
function readJsonFromStorage(key, defaultValue) {
    const valueFromStorage = localStorage.getItem(key);
    if (valueFromStorage) {
        return JSON.parse(valueFromStorage);
    }
    return defaultValue;
}
function writeJsonToStorage(key, value) {
    localStorage.setItem(key, JSON.stringify(value));
}
